.real-support{
    background-color: #f2f2f2;
    background-position: left top;
    background-repeat: no-repeat;
    padding-top: 55px;
    padding-right: 30px;
    padding-bottom: 40px;
    padding-left: 30px;
    border-top-width: 0px;
    border-bottom-width: 0px;
    border-color: #eae9e9;
    border-top-style: solid;
    border-bottom-style: solid;
}
.support-container{
    padding-left: 0;
    padding-right: 0;
}
.support-wrapper{
    display: flex;
    justify-content: center;
    font-size: 40px
}
.support-content{
    display: flex;
    text-align: center;
    font-size: 18px;
    color: #747474;
}     