
.avadaroll{
    background-color: rgba(255,255,255,0);
    background-image: url(https://avada.theme-fusion.com/wp-content/uploads/2014/09/wood_dark_bkgd_bw1.jpg);
    background-position: left top;
    background-repeat: repeat;
    padding-top: 65px;
    padding-right: 30px;
    padding-bottom: 30px;
    padding-left: 30px;
    border-top-width: 0px;
    border-bottom-width: 0px;
    border-color: #eae9e9;
    border-top-style: solid;
    border-bottom-style: solid;
}
.avadaroll-wrapper{
    padding: 0px 0px 0px 0px;
    background-position: left top;
    background-repeat: no-repeat;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
}
.flip-right{
    position: relative;
    z-index: 1;
    padding: 0;
    margin: 0;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}
.flip-box-wrapper{
    min-height: 247px;
    position: relative;
    margin-bottom: 15px;
    perspective: 1000px;
    background-color: white;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
.flip-icon-front{
    background-color: #333333;
    border-color: #333333;
    height: 64px;
    width: 64px;
    border: 1px solid;
    -webkit-border-radius: 50%;
    border-radius: 50%;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
  
}
.flip-box-inner{
    
    text-align: center;
    padding-top: 20px;
}
.flip-box-circle {
    text-align: center;
    margin-left: 130px;
}
.f-icon{
    padding-top: 14px;
    color: white;
    font-size: 30px;
   
}
.flip-front-title {
    font-size: 25px;
    
}
.flip-back-inner{
    text-align: center;
    padding-top: 20px;
}
.flip-box-back{
    background-color: rgb(160, 206, 78);
    border-color: rgba(0, 0, 0, 0);
    border-radius: 4px;
    border-style: solid;
    border-width: 1px;
    color: rgb(255, 255, 255);
    transition-duration: 0.4s;
    bottom: 0px;
   
}

.f-button-wrapper{
  /* margin-top: 26px; */
  margin: 20px auto 0;
  border-radius: 2px;
  padding: 10px 9px 10px 9px;

}
.f-button{
    background: #97c04c;
    border: 1px solid rgba(255,255,255,.7);
    padding: 13px 29px;
    line-height: 17px;
    font-size: 14px;
}
.flip-box-back{
    transform: rotateY(180deg);
   
}
.flip-back-title{
    color: #eeeded;
    font-size: 14px;
}
.flip-box-front, .flip-box-back{
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}
.flip-right{
    perspective: 1000px;
}
/* .flip-box-wrapper:hover .flip-box-back, .flip-box-wrapper:hover .flip-box-front{
    transform: rotateY(180deg);
} */
.flip-right:hover .flip-box-wrapper{
    transform: rotateY(180deg);
}