.list-menu{
  display: inline-block;
 
} 
.menu-item{
    display: inline-block;
    margin-right: 15px;
    position: relative;
    
}

.menu-title{
    
    width: 100%;
}
.list-item{
    color: black;
}
.item-menu {
    list-style: none;
    margin: 0;
    padding: 0;
}
.drop-btn{
  color: #4CAF50;
  padding: 16px;
  font-size: 16px;
  border: none;
  text-decoration: none;
}
.slider-btn{
    padding: 16px;
    font-size: 16px;
    border: none;
}
.banner-title{
    font-size: 55px;
}
.item-content, .slider-content{
    top: 40px;
    left: -155px;
    position: absolute;
    display: none;
    
    min-width: 350px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
   
    transition: opacity .2s ease-in;
    z-index: 10;
    border-top:  2px solid #a0ce4e;
    
}

.item-content a{
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
.item-content  { background-color: #edecec;}

.menu-item:hover .item-content {display: block;}

.menu-item:hover  {color: #ddd;}



.item-holder{
   border-color: #edecec;
    
    width: 1100px;
}
.menu-item:hover{
    
    color: #a0ce4e;
}
ul{
    list-style-type: none;
}
.fusion-main-menu li {
    float: left;
    margin: 0;
    padding: 0;
    position: relative;
    cursor: pointer;
}

