.title{
    margin-top: 10px;
    font-size: 30px;
    margin-left: 15px;
}
.fushion-megamenu{
    padding: 0;
    border-right: 1px solid grey;
    height: 97%;
    width: 100%;
}


.menu-item-details:hover{
    background-color: #f1f1f1;
}
.fusion-megamenu-bullet::before{
   
    font-family: "FontAwesome";
    content: '\f0da';
}
