.rate-star{
    background-color: rgba(255,255,255,0);
    background-position: left top;
    background-repeat: no-repeat;
    padding-top: 90px;
    padding-right: 30px;
    padding-bottom: 55px;
    padding-left: 30px;
    border-top-width: 0px;
    border-bottom-width: 0px;
    border-color: #eae9e9;
    border-top-style: solid;
    border-bottom-style: solid;
}
.left-content , .right-content{
    display: inline-block;
}
.left-content{
    margin-top: 0px;
    margin-bottom: 20px;
    width: 48%;
    margin-right: 4%;
    float: left;
}
.right-content{
    margin-top: 0px;
    margin-bottom: 20px;
    width: 48%;

}
.rate-text ,.rate-star2{
    font-size: 18px;
    text-align:  center;
}
.bottom-content{
    text-align: center;
}
.rate-btn{
    margin-top: 40px;
    color: white
}
.btn-reviews{
   
    border: 1px solid #ffffff;
    padding: 17px 40px;
    background-color: #A0CE4E;
}
.rate-text1{
    color: #a0ce4e;
    font-size: 56px;
    line-height: normal;
    margin-bottom: 40px;
}