
.scroll-btn {
    height: 35px;
    width: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: .8;
    z-index: 100000;
    position: fixed;
    bottom: 0;
    right: 75px;
    background-color: #333;
    font-size: 18px;
    font-weight: 400;
    color: #fff;
    border-radius: 4px 4px 0 0;
    transition-duration: .4s;
  }
  .hide-scroll {
    transition-duration: .4s;
    opacity: 0;
    visibility: hidden;
  }
  
  .show-scroll {
    transition-duration: .4s;
    opacity: 1;
    visibility: visible;
  }